import React from 'react';
import { DialogModalProps, ModalSize } from 'core/types/modals';
import ModalOverlay from 'shared/ModalOverlay';
import { cssDecorator } from '@front-toolkit/utils';
import { ScrollBox } from 'shared/ScrollBox';
import Text from 'core/src/typography/Text'; // eslint-disable-line import/no-named-as-default

import { useScrollResizeObserver } from '../useScrollResizeObserver';
import Footer from '../Footer';
import Header from '../Header';

import styles from './styles.scss';

const cd = cssDecorator(styles);

const descriptionSizeProperty: Record<ModalSize, 'small' | 'extra_small'> = {
  default: 'small',
  small: 'extra_small'
};

export const Dialog: React.FC<DialogModalProps> = ({
  label,
  description,
  onClose,
  onOverlayClick,
  footerItemsLeft,
  footerItemsRight,
  isHTMLDescription,
  children,
  size = 'default',
  zIndex = 400,
  selector = 'body',
  dataTestId = 'ds-dialog'
}) => {
  const sizeModifier = { [size]: true };
  const hasFooter = Boolean(footerItemsLeft?.length || footerItemsRight?.length);
  const bottomIndentClassName = cd('bottom_indent', sizeModifier);
  const bodyClassName = cd(
    'body',
    sizeModifier,
    !hasFooter || !description ? bottomIndentClassName : undefined
  );
  const {
    setInnerWrapperRef,
    scrollParentClassName,
    overrideScrollBoxClassName,
    scrollParentStyle
  } = useScrollResizeObserver(bodyClassName);
  const contentClassName = [styles.content, hasFooter && bottomIndentClassName].filter(Boolean).join(' ');

  return (
    <ModalOverlay
      selector={ selector }
      onClick={ onOverlayClick }
      zIndex={ zIndex }
    >
      <div
        className={ cd('wrapper', sizeModifier) }
        data-test-id={ dataTestId }
      >
        <Header
          label={ label }
          size={ size }
          onClose={ onClose }
        />
        <div
          className={ scrollParentClassName }
          style={ scrollParentStyle }
          role="main"
        >
          { description && (
            <ScrollBox overrideWrapperClassName={ overrideScrollBoxClassName }>
              <div className={ contentClassName } ref={ setInnerWrapperRef }>
                { children && <div className={ styles.children }>{ children }</div> }
                { isHTMLDescription
                  // eslint-disable-next-line react/no-danger
                  ? <div dangerouslySetInnerHTML={ { __html: description } } />
                  : <Text size={ descriptionSizeProperty[size] } color="secondary">{ description }</Text> }
              </div>
            </ScrollBox>
          ) }
        </div>
        <Footer
          size={ size }
          footerItemsLeft={ footerItemsLeft }
          footerItemsRight={ footerItemsRight }
        />
      </div>
    </ModalOverlay>
  );
};
